.bx--apim-list-container {
  overflow: hidden; }
  .bx--apim-list-container .bx--apim-list-container-title {
    min-height: 50px;
    font-weight: bold; }
  .bx--apim-list-container .bx--apim-list-container-title + .bx--apim-list-container-body {
    max-height: calc(100% - 50px); }
  .bx--apim-list-container .bx--apim-list-container-body {
    max-height: 100%; }
  .bx--apim-list-container .bx--apim-list-container-body-scrollable {
    overflow-y: scroll; }



